@keyframes rotate {
  0% {
    transform-origin: bottom;
    transform: rotate(0);
  }

  100% {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  animation: none;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minutes {
  z-index: 3;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  top: 20vmin;
}

.stopwatch__minutes--speed-up {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch__seconds {
  z-index: 2;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__seconds--speed-up {
  animation: 10s linear infinite rotate;
}

